.searchbar {
    top: 0;
    left: 0;
    position: sticky;
    z-index: 1100;
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 64px;
    padding-right: 24px;
    padding-left: 24px;
    padding-top: 12px;
    padding-bottom: 12px;
    color: #fff;
    background-color: #3f51b5;
    box-shadow: 0px 2px 4px -1px rgba(0, 0, 0, 0.2),
        0px 4px 5px 0px rgba(0, 0, 0, 0.14), 0px 1px 10px 0px rgba(0, 0, 0, 0.12);
}



.searchForm {
    display: flex;
    align-items: center;
    width: 100%;
    max-width: 600px;
    background-color: #fff;
    border-radius: 3px;
    overflow: hidden;
    background-color: #3f51b5;
}

.searchFormButton {
    /* display: inline-block; */
    width: 48px;
    height: 48px;
    border: 0;
    display: flex;
        justify-content: center;
        align-items: center;
    opacity: 1;
    transition: opacity 250ms cubic-bezier(0.4, 0, 0.2, 1);
    cursor: pointer;
    outline: none;
    margin-right: 5px;
    border:#000000;
        border-radius: 15px;
        box-shadow: inset 5px 5px 10px #000000;
        
        background-image: url('https://image.flaticon.com/icons/svg/149/149852.svg');
            background-size: 40%;
            background-repeat: no-repeat;
            background-position: center;
        
}

.searchFormButton:hover {
    opacity: 0.8;
}

