.searchFormInput {
    display: inline-block;
    width: 100%;
    font: inherit;
    font-size: 20px;
    border: none;
    outline: none;
    padding-left: 4px;
    padding-right: 4px;

    padding: 10px 20px;
        border: none;
        border-radius: 15px;
        box-shadow: inset 5px 5px 10px #000000;
}

.searchFormInput::placeholder {
    font: inherit;
    font-size: 18px;
}



.searchFormInput:focus {
    outline: none;
}